import {
  Cards,
  ChatsCircle,
  CirclesThreePlus,
  FacebookLogo,
  Gift,
  Question,
  ShoppingCartSimple,
  SidebarSimple,
  Tag,
  UserCircle,
} from 'phosphor-react'
import NavigationCard from './NavigationCard'
import React from 'react'

const NavigationButtons = () => {
  return (
    <div className="my-3">
      <div className="d-flex">
        <NavigationCard
          icon={<CirclesThreePlus size={25} />}
          title=" "
          path="/categories"
          color="#ffff66"
        />
        <NavigationCard
          icon={<Tag size={25} />}
          title=""
          path="/products"
          color="#c29aff"
        />
        <NavigationCard
          icon={<ShoppingCartSimple size={25} />}
          title=""
          path="/orders"
          color="#fdaa48"
        />
        <NavigationCard
          icon={<SidebarSimple size={25} />}
          title=""
          path="/sliders"
          color="#99cccc"
        />
        <NavigationCard
          icon={<Cards size={25} />}
          title=""
          path="/banners"
          color="#e1aab0"
        />
      </div>
      <div className="d-flex">
        <NavigationCard icon={<UserCircle size={25} />} title="" path="/users" />
        <NavigationCard icon={<Gift size={25} />} title="" path="/coupons" />
        <NavigationCard
          icon={<FacebookLogo size={25} />}
          title="  "
          path="/socials"
        />
        <NavigationCard icon={<Question size={25} />} title=" " path="/about_us" />
        <NavigationCard
          icon={<ChatsCircle size={25} />}
          title=" "
          path="/feedBacks"
        />
      </div>
    </div>
  )
}

export default NavigationButtons
