/* eslint-disable prettier/prettier */
/* eslint-disable jsx-a11y/alt-text */
/* eslint-disable prettier/prettier */
/* eslint-disable react/react-in-jsx-scope */
/* eslint-disable prettier/prettier */
import {
  CAlert,
  CButton,
  CCardBody,
  CCol,
  CForm,
  CFormCheck,
  CFormInput,
  CFormSelect,
  CRow,
} from '@coreui/react'
import { PlusCircle } from 'phosphor-react'
import { useState } from 'react'
import { Spinner } from 'react-bootstrap'
import { ToastContainer } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css'
import { AppBreadcrumb } from 'src/components'
import AppCard from 'src/components/AppCard'
import PageTitle from 'src/components/PageTitle'
import { Theme } from 'src/constants/colors'
import { useBannerAdding } from 'src/hooks/banners/useBannerAdding'
import { useCategories } from 'src/hooks/categories/useCategories'
import { useLanguage } from 'src/hooks/general/useLanguage'
import { useWidth } from 'src/hooks/general/useWidth'
import { useProducts } from 'src/hooks/products/useProducts'

const AddBanner = () => {
  const {
    bannerName,
    setBannerName,
    bannerImage,
    setBannerImage,
    bannerImageEn,
    setBannerImageEn,
    bannerImageMobile,
    setBannerImageMobile,
    bannerImageMobileEn,
    setBannerImageMobileEn,
    handleSubmit,
    validated,
    loading,
    visible,
    setVisible,
    bannerTitleAr,
    setBannerTitleAr,
    bannerTitleEn,
    setBannerTitleEn,
    bannerTitleHeb,
    setBannerTitleHeb,
    type,
    setType,
    dataId,
    setDataId,
  } = useBannerAdding()

  const { width } = useWidth()
  const { isArabic, isEnglish, isHebrew } = useLanguage()
  const { categories } = useCategories()
  const { products } = useProducts()

  return (
    <CRow>
      <CCol xs>
        <PageTitle title=" " icon={<PlusCircle size={25} />} />
        <AppBreadcrumb />
        <AppCard>
          <CCardBody>
            <div className="column">
              <div className="row">
                <div className="col">
                  <CForm
                    className="row g-3 needs-validation"
                    noValidate
                    validated={validated}
                    onSubmit={handleSubmit}
                  >
                    <CCol sm={12}>
                      <CFormInput
                        type="text"
                        placeholder="  "
                        feedbackInvalid="  "
                        id="categoryNameAr"
                        label="  "
                        required
                        onChange={(e) => {
                          setBannerName(e.target.value)
                        }}
                        value={bannerName}
                      />
                    </CCol>
                    {isArabic == 'true' && (
                      <CCol sm={width < 768 ? 12 : null}>
                        <CFormInput
                          type="text"
                          placeholder="   "
                          feedbackInvalid="   "
                          id="categoryNameAr"
                          label="   "
                          required
                          onChange={(e) => {
                            setBannerTitleAr(e.target.value)
                          }}
                          value={bannerTitleAr}
                        />
                      </CCol>
                    )}
                    {isEnglish == 'true' && (
                      <CCol sm={width < 768 ? 12 : null}>
                        <CFormInput
                          type="text"
                          placeholder="   "
                          feedbackInvalid="   "
                          id="categoryNameAr"
                          label="   "
                          required
                          onChange={(e) => {
                            setBannerTitleEn(e.target.value)
                          }}
                          value={bannerTitleEn}
                        />
                      </CCol>
                    )}
                    {isHebrew == 'true' && (
                      <CCol sm={width < 768 ? 12 : null}>
                        <CFormInput
                          type="text"
                          placeholder="   "
                          feedbackInvalid="   "
                          id="categoryNameAr"
                          label="   "
                          required
                          onChange={(e) => {
                            setBannerTitleHeb(e.target.value)
                          }}
                          value={bannerTitleHeb}
                        />
                      </CCol>
                    )}
                    <div className="mb-3">
                      <CFormInput
                        label=" "
                        type="file"
                        id="categoryPic"
                        feedbackInvalid="  "
                        required
                        onChange={(e) => {
                          setBannerImage(e.target.files[0])
                        }}
                      />
                      {bannerImage && (
                        <img
                          src={bannerImage ? URL.createObjectURL(bannerImage) : ''}
                          style={{ margin: '10px' }}
                          width={300}
                          height={300}
                        />
                      )}
                    </div>
                    <div className="mb-3">
                      <CFormInput
                        label="   "
                        type="file"
                        id="categoryPic"
                        feedbackInvalid="    "
                        required
                        onChange={(e) => {
                          setBannerImageEn(e.target.files[0])
                        }}
                      />
                      {bannerImageEn && (
                        <img
                          src={bannerImageEn ? URL.createObjectURL(bannerImageEn) : ''}
                          style={{ margin: '10px' }}
                          width={300}
                          height={300}
                        />
                      )}
                    </div>
                    <div className="mb-3">
                      <CFormInput
                        label=" "
                        type="file"
                        id="categoryPic"
                        feedbackInvalid="  "
                        required
                        onChange={(e) => {
                          setBannerImageMobile(e.target.files[0])
                        }}
                      />
                      {bannerImageMobile && (
                        <img
                          src={bannerImageMobile ? URL.createObjectURL(bannerImageMobile) : ''}
                          style={{ margin: '10px' }}
                          width={300}
                          height={300}
                        />
                      )}
                    </div>
                    <div className="mb-3">
                      <CFormInput
                        label="   "
                        type="file"
                        id="categoryPic"
                        feedbackInvalid="    "
                        required
                        onChange={(e) => {
                          setBannerImageMobileEn(e.target.files[0])
                        }}
                      />
                      {bannerImageMobileEn && (
                        <img
                          src={bannerImageMobileEn ? URL.createObjectURL(bannerImageMobileEn) : ''}
                          style={{ margin: '10px' }}
                          width={300}
                          height={300}
                        />
                      )}
                    </div>
                    <CCol md={12}>   </CCol>
                    <CCol md={2} className="d-flex justify-content-between">
                      <CFormCheck
                        button={{ color: 'secondary', variant: 'outline' }}
                        type="radio"
                        name="options-outlined"
                        id="secondary-outlined"
                        autoComplete="off"
                        label=""
                        value="product"
                        checked={type === 'product'}
                        onChange={(e) => {
                          setType(e.target.value)
                        }}
                      />
                      <CFormCheck
                        button={{ color: 'secondary', variant: 'outline' }}
                        type="radio"
                        name="options-outlined"
                        id="danger-outlined"
                        autoComplete="off"
                        label=" "
                        value="category"
                        checked={type === 'category'}
                        onChange={(e) => {
                          setType(e.target.value)
                        }}
                      />
                    </CCol>
                    {type == 'product' ? (
                      <CCol md={12}>
                        <CFormSelect
                          id="productCategory"
                          label=" "
                          feedbackInvalid="  "
                          value={dataId}
                          onChange={(e) => setDataId(e.target.value)}
                          required
                        >
                          <option selected="" value="">  </option>
                          {products?.map((item, index) => (
                            <option value={item.id} key={item.id}>
                              {item.name_ar}
                            </option>
                          ))}
                        </CFormSelect>
                      </CCol>
                    ) : (
                      <CCol md={12}>
                        <CFormSelect
                          id="productCategory"
                          label=" "
                          feedbackInvalid="  "
                          value={dataId}
                          onChange={(e) => setDataId(e.target.value)}
                          required
                        >
                          <option selected="" value="">  </option>
                          {categories?.map((item, index) => (
                            <option value={item.id} key={item.id}>
                              {item.name_ar}
                            </option>
                          ))}
                        </CFormSelect>
                      </CCol>
                    )}

                    {loading ? (
                      <Spinner animation="border" role="status" style={{ margin: '10px' }}>
                        <span className="visually-hidden">Loading...</span>
                      </Spinner>
                    ) : (
                      <CButton
                        style={{
                          width: '200px',
                          marginTop: '30px',
                          backgroundColor: Theme.primary1,
                          border: 'none',
                        }}
                        type="submit"
                      >
                        <span style={{ fontWeight: 'bolder' }}> </span>
                      </CButton>
                    )}
                    <CAlert
                      color="danger"
                      dismissible
                      visible={visible}
                      onClose={() => setVisible(false)}
                    >
                             
                    </CAlert>
                  </CForm>
                </div>
              </div>
            </div>
          </CCardBody>
        </AppCard>
      </CCol>
      <ToastContainer />
    </CRow>
  )
}

export default AddBanner
