/* eslint-disable prettier/prettier */
/* eslint-disable no-unused-vars */
import React from 'react'
import {
  CButton,
  CCol,
  CFormCheck,
  CRow,
  CTable,
  CTableBody,
  CTableDataCell,
  CTableHead,
  CTableHeaderCell,
  CTableRow,
} from '@coreui/react'

import { useNavigate } from 'react-router-dom'
import { Spinner } from 'react-bootstrap/esm'
import { CheckSquare, CirclesThreePlus } from 'phosphor-react'
import { ToastContainer } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css'
import { useCategories } from 'src/hooks/categories/useCategories'
import AppDialog from 'src/components/AppDialog'
import PageTitle from 'src/components/PageTitle'
import { Theme } from 'src/constants/colors'
import AppTooltip from 'src/components/Tooltip'
import { AppBreadcrumb } from 'src/components'
import { useLanguage } from 'src/hooks/general/useLanguage'
import image from '../../assets/images/image.png'
import EmptyPage from 'src/components/EmptyPage'
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd'

const Categories = () => {
  const navigate = useNavigate()
  const {
    categories,
    loading,
    showDialog,
    confirmDelete,
    executeDelete,
    cancelDelete,
    setFilterValue,
    markedItems,
    showDeleteDialog,
    toggleMarkedItem,
    markAll,
    deleteMarked,
    cancelDeleteMarked,
    executeDeleteMarked,
    searchQuery,
    setSearchQuery,
    handleOnDragEnd,
  } = useCategories()

  const { isArabic, isEnglish, isHebrew } = useLanguage()

  return (
    <CRow>
      <CCol xs>
        <PageTitle
          title=" "
          icon={<CirclesThreePlus size={25} />}
          addText=" "
          addPath="add_category"
          filterValue={searchQuery}
          setFilterValue={setSearchQuery}
          filterItems={true}
          addItem={true}
          mark={true}
          deleteMarked={deleteMarked}
          markedItems={markedItems}
        />
        <AppBreadcrumb />
        <div>
          {loading ? (
            <div className="h-100 w-100 d-flex justify-content-center align-items-center py-5 my-5">
              <Spinner animation="border" role="status">
                <span className="visually-hidden">Loading...</span>
              </Spinner>
            </div>
          ) : (
            <>
              {categories.length > 0 ? (
                <div
                  className="rounded my-3"
                  style={{ backgroundColor: Theme.white, boxShadow: '0px 2px 3px #c8c8c8' }}
                >
                  {/* <DragDropContext onDragEnd={handleOnDragEnd}>
                    <Droppable droppableId="categories">
                      {(provided) => ( */}
                  <CTable
                    align="middle"
                    hover
                    responsive
                    striped
                    className=" mb-3 categories"
                    // {...provided.droppableProps}
                    // ref={provided.innerRef}
                    width={1000}
                  >
                    <CTableHead color="light">
                      <CTableRow>
                        <CTableHeaderCell className="text-center" style={{ width: 100 }}>
                          <CButton
                            style={{
                              backgroundColor: Theme.primary1,
                              border: 'none',
                            }}
                            onClick={markAll}
                          >
                            <CheckSquare size={20} /> 
                          </CButton>
                        </CTableHeaderCell>
                        <CTableHeaderCell className="text-center"></CTableHeaderCell>
                        <CTableHeaderCell className="text-center"></CTableHeaderCell>
                        <CTableHeaderCell className="text-center">  </CTableHeaderCell>
                      </CTableRow>
                    </CTableHead>
                    <CTableBody>
                      {categories.map((item, index) => (
                        <CTableRow
                        key={index}
                          v-for="item in tableItems"
                        >
                          <CTableDataCell className="text-center fw-medium">
                            <div className="d-flex justify-content-center align-items-center">
                              <CFormCheck
                                id="index"
                                className="customCheckbox"
                                label=" "
                                reverse
                                checked={markedItems.find((id) => id === item.id)}
                                onChange={() => {
                                  toggleMarkedItem(item.id)
                                }}
                              />
                            </div>
                          </CTableDataCell>
                          <CTableDataCell>
                            <div className="d-flex flex-column h-100 align-items-center">
                              {isArabic === 'true' && (
                                <button
                                  className="hovarableText"
                                  onClick={() =>
                                    navigate(`/edit_category/${item.id}`, {
                                      state: { item },
                                    })
                                  }
                                >
                                  {item.name_ar}
                                </button>
                              )}
                              {isEnglish === 'true' && (
                                <button
                                  className="hovarableText"
                                  onClick={() =>
                                    navigate(`/edit_category/${item.id}`, {
                                      state: { item },
                                    })
                                  }
                                >
                                  {item.name_en}
                                </button>
                              )}
                              {isHebrew === 'true' && (
                                <button
                                  className="hovarableText"
                                  onClick={() =>
                                    navigate(`/category/${item.id}`, { state: { item } })
                                  }
                                >
                                  {item.name_he}
                                </button>
                              )}
                            </div>
                          </CTableDataCell>

                          <CTableDataCell className="text-center">
                            <img
                              src={item.image ? item.image : image}
                              onError={(e) => {
                                e.target.onError = null
                                e.target.src = image
                              }}
                              alt="category"
                              width="100"
                              height="100"
                            />
                          </CTableDataCell>

                          <CTableDataCell>
                            <div style={{ display: 'flex', justifyContent: 'center' }}>
                              <AppTooltip type="delete" onClick={() => confirmDelete(item.id)} />
                              <AppTooltip
                                type="edit"
                                onClick={() =>
                                  navigate(`/edit_category/${item.id}`, {
                                    state: { item },
                                  })
                                }
                              />
                            </div>
                          </CTableDataCell>
                        </CTableRow>
                      ))}
                    </CTableBody>
                  </CTable>
                  {/* )}
                    </Droppable>
                  </DragDropContext> */}
                </div>
              ) : (
                <EmptyPage emptyItems="" />
              )}
            </>
          )}
        </div>
      </CCol>
      <ToastContainer />
      <AppDialog
        title="       "
        open={showDialog}
        onClose={cancelDelete}
        actionCancel={cancelDelete}
        actionExecute={executeDelete}
        deleteDialog={true}
      />
      <AppDialog
        title="           "
        open={showDeleteDialog}
        onClose={cancelDeleteMarked}
        actionCancel={cancelDeleteMarked}
        actionExecute={executeDeleteMarked}
        deleteDialog={true}
      />
    </CRow>
  )
}

export default Categories
